import { useParams, useNavigate, Link } from "react-router-dom";
import Footer from "../../components/footer";
import LanguageBar from "../../components/HomeComponents/topbar/languagebar";
import NavigationBar from "../../components/HomeComponents/topbar/navigationbar";
import axios from "axios";
import { useState } from "react";
import { useTranslation } from "react-i18next";

const Agent = () => {

  const { t } = useTranslation()
  const [name, setName] = useState("");

  const data = {
    "andrei-makeev": {
      name: "Andrei Makeev",
      address: "Shark Hotel Residence Office 4 Inonu Cd Girne, Kyrenia.",
      phone: "+90 533 883 55 38",
      email: "andrey@qsyrys.com",
      occupation: "Sales Manager",
      officeAddress: "+90 533 883 55 38",
      img: "https://www.qsyrys.com/wp-content/uploads/2022/07/WhatsApp-Image-2022-07-06-at-11.07.30-PM.webp",
    },
    "bandar-zuhair-ali-asiri": {
      name: "Bandar Zuhair Ali Asiri",
      address: "Shark Hotel Residence Office 4 Inonu Cd Girne, Kyrenia.",
      phone: "+905428885538",
      licenses: "ms.23103",
      occupation: "Rental Manager",
      email: "bandar.z@qsyrys.com",
      img: "https://www.qsyrys.com/wp-content/uploads/2022/12/agent-scaled.jpg",
    },
    "temile-emmanuel": {
      name: "Temile Emmanuel",
      address: "Shark Hotel Residence Office 4 Inonu Cd Girne, Kyrenia.",
      email: "emmanuel@qsyrys.com",
      phone: "+90 533 883 55 38",
      occupation: "Sales Manager",
      img: "https://www.qsyrys.com/wp-content/uploads/2017/01/Untitled-1-copy-270x340.png",
    },
  };

  const { user } = useParams();

  const navigate = useNavigate();

  const selectedAgent = data[user];

  if (selectedAgent === undefined) {
    navigate("/404");
  }

  const [email, setEmail] = useState("");

  const [phone, setPhone] = useState("");

  const [msg, setMsg] = useState("");

  const [loading, setLoading] = useState(false);

  const [error, setError] = useState("");

  const [success, setSuccess] = useState("");

  const handleSubmit = async () => {
    if (loading) return;

    if (!name || !phone || !email || !msg) {

      setError(t('field_error'));
      return;
    }

    try {
      setLoading(true);
      setError("");
      setSuccess("");

      const res = await axios.post("/api/qsyrys/send", {
        name,
        email,
        agent_name: user,
        phone,
        message: msg,
      });

      setSuccess(t('success_msg'));

      document.querySelector(".name-error")?.scrollIntoView();

      setLoading(false);
    } catch (err) {
      setError(err?.response?.data.message || t("general_error"));

      document.querySelector(".name-error")?.scrollIntoView();

      setLoading(false);

    }
  };

  return (
    <div
      className="home page-template page-template-elementor_header_footer page page-id-58 wp-embed-responsive beyot-class chrome header-is-float header-sticky-fix-style elementor-default elementor-template-full-width elementor-kit-4695 elementor-page elementor-page-58 e--ua-blink e--ua-chrome e--ua-webkit elementor elementor-58"
      id="wrapper"
    >
      <LanguageBar />
      <NavigationBar />

      <div id="wrapper-content" className="clearfix ">
        <section className="page-title page-title-background py-[120px]">
          <div
            className="page-title-background page-title-parallax"
            style={{ backgroundImage: `url(${selectedAgent.img})` }}
            data-stellar-background-ratio="0.5"
          ></div>
          <div
            className="vc_row-background-overlay"
            style={{
              backgroundColor: "rgba(0,0,0,0.68)",
            }}
          ></div>
          <div className="container">
            <div className="page-title-inner">
              <div className="page-title-main-info">
                <h1>{selectedAgent.name}</h1>
              </div>

              <ul className="breadcrumbs">
                <li>
                  <Link to="/" className="home">
                    {t("home")}
                  </Link>
                </li>
                <li>
                  <span>{selectedAgent.name}</span>
                </li>{" "}
              </ul>
            </div>
          </div>
        </section>
        <div id="primary-content" className="pd-top-100 pd-bottom-100">
          <div className="container clearfix">
            <div className="single-agent-inner">
              <div id="container">
                <div id="content" role="main">
                  <link
                    rel="stylesheet"
                    id="ere_single-agent-css"
                    href="https://www.qsyrys.com/wp-content/plugins/essential-real-estate/public/assets/css/single-agent.min.css"
                    type="text/css"
                    media="all"
                  />
                  <div
                    id="agent-486"
                    className="ere-agent-single-wrap ere-agent-single post-486 agent type-agent status-publish has-post-thumbnail hentry agency-syrys-investment-ltd"
                  >
                    <div className="single-agent-element agent-single">
                      <div className="agent-single-inner row">
                        <div className="agent-avatar text-center col-md-3 col-sm-12">
                          <img
                            width="270"
                            height="340"
                            src={`${selectedAgent.img}`}
                            onError={(e) => {
                              e.target.src =
                                "https://www.qsyrys.com/wp-content/uploads/2017/12/no-image.jpg";
                            }}
                            alt={selectedAgent.name}
                            title={selectedAgent.name}
                            decoding="async"
                            loading="lazy"
                          />
                        </div>
                        <div className="agent-content col-md-5 col-sm-12">
                          <div className="agent-content-top">
                            <h2 className="agent-title">
                              {selectedAgent.name}
                            </h2>
                            <div className="agent-social">
                              <a
                                title="Facebook"
                                href="https://www.facebook.com/realsyrys"
                              >
                                <i className="fa fa-facebook"></i>
                              </a>
                              <a title="Email" href="mailto:andrey@qsyrys.com">
                                <i className="fa fa-envelope"></i>
                              </a>
                              <a
                                title="Linkedin"
                                href="https://www.linkedin.com/company/syrysinvestment"
                              >
                                <i className="fa fa-linkedin"></i>
                              </a>
                              <a
                                title="Instagram"
                                href="https://www.instagram.com/qsyrys_ru/"
                              >
                                <i className="fa fa-instagram"></i>
                              </a>
                              <a
                                title="Youtube"
                                href="https://www.youtube.com/@real_syrys"
                              >
                                <i className="fa fa-youtube-play"></i>
                              </a>
                            </div>
                            <span className="agent-position">
                              {selectedAgent.occupation}
                            </span>
                          </div>
                          <div className="agent-contact agent-info">
                            <div>
                              <i className="fa fa-map-marker"></i>
                              <strong>{t("address")}:</strong>
                              <span>{selectedAgent.address}</span>
                            </div>
                            <div>
                              <i className="fa fa-envelope"></i>
                              <strong>{t("email")}:</strong>
                              <a
                                style={{
                                  display: "inline",
                                }}
                                href={`mailto:${selectedAgent.email}`}
                                title={t('email')}
                              >
                                <span>{selectedAgent.email}</span>
                              </a>
                            </div>
                            <div>
                              <i className="fa fa-phone"></i>
                              <strong>{t("phone")}:</strong>
                              <span>{selectedAgent.phone}</span>
                            </div>
                            <div>
                              <i className="fa fa-link"></i>
                              <strong>{t("website")}:</strong>
                              <a
                                style={{
                                  display: "inline",
                                }}
                                href="https://qsyrys.com"
                                title={t('website')}
                              >
                                <span>https://qsyrys.com</span>
                              </a>
                            </div>
                            <hr className="mg-top-20" />
                            <div className="agent-agency">
                              <strong>{t("agency")}:</strong>
                              <a href="https://www.qsyrys.com/">
                                SYRYS Investment Ltd
                              </a>{" "}
                            </div>
                            <div>
                              <strong>{t("company")}:</strong>
                              <span>Syrys Investment Ltd</span>
                            </div>
                            <div>
                              <strong>{t("office_number")}:</strong>
                              <span>{selectedAgent.phone}</span>
                            </div>
                            <div>
                              <strong>{t("office_address")}:</strong>
                              <span>{selectedAgent.address}</span>
                            </div>
                          </div>
                        </div>
                        <div className="contact-agent col-md-4 col-sm-12">
                          <div className="ere-heading-style2 contact-agent-title">
                            <h2>{t("contact")}</h2>
                          </div>

                          {(Boolean(error.length) ||
                            Boolean(success.length)) && (
                            <div className="name-error form-error mb-2">
                              {error} {success}
                            </div>
                          )}

                          <form
                            action="#"
                            method="POST"
                            onSubmit={(e) => {
                              e.preventDefault();
                              return false;
                            }}
                            id="contact-agent-form"
                          >
                            <input
                              type="hidden"
                              name="target_email"
                              value={selectedAgent["email"]}
                            />

                            <div className="form-group">
                              <input
                                className="form-control"
                                name="sender_name"
                                value={name}
                                type="text"
                                onChange={(e) => {
                                  setName(e.target.value);
                                  setSuccess("");
                                  setError("");
                                }}
                                placeholder={t("full_name")}
                              />

                              <div className="hidden name-error form-error">
                                {t("full_name_error")}
                              </div>
                            </div>
                            <div className="form-group">
                              <input
                                className="form-control"
                                name="sender_phone"
                                type="text"
                                value={phone}
                                onChange={(e) => {
                                  setPhone(e.target.value);
                                  setSuccess("");
                                  setError("");
                                }}
                                placeholder={t("phone_number")}
                              />

                              <div className="hidden phone-error form-error">
                                {t("phone_number_error")}
                              </div>
                            </div>
                            <div className="form-group">
                              <input
                                className="form-control"
                                name="sender_email"
                                value={email}
                                onChange={(e) => {
                                  setEmail(e.target.value);
                                  setSuccess("");
                                  setError("");
                                }}
                                type="email"
                                placeholder={t("email_address")}
                              />

                              <div
                                className="hidden email-error form-error"
                                data-not-valid="Your Email address is not Valid!"
                                data-error="Please enter your Email!"
                              >
                                {t("email_address_error")}
                              </div>
                            </div>
                            <div className="form-group">
                              <textarea
                                className="form-control"
                                name="sender_msg"
                                value={msg}
                                rows="5"
                                onChange={(e) => {
                                  setMsg(e.target.value);
                                  setSuccess("");
                                  setError("");
                                }}
                                placeholder={t("msg")}
                              ></textarea>

                              <div className="hidden message-error form-error">
                                {t("msg_error")}
                              </div>
                            </div>

                            <button
                              type="submit"
                              onClick={handleSubmit}
                              className="agent-contact-btn btn btn-block"
                            >
                              {loading ? t('sending') : t("submit_request")}
                            </button>
                            <div className="form-messages"></div>
                          </form>
                        </div>
                      </div>
                    </div>{" "}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </div>
  );
};

export default Agent;
